html {
  font-family: Roboto, sans-serif;
}

p, h3 {
  margin: 0;
  padding: 0;
}

.body {
  --white: #fff;
  --blue: #00acdc;
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info {
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.reviews {
  min-width: 166px;
  margin: 16px 0 24px;
  display: flex;
}

.reviews__text {
  justify-content: space-between;
  padding-left: 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  display: flex;
}

.stars {
  justify-content: space-between;
  min-width: 60%;
  line-height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 60%;
  line-height: 16px;
}

.stars--0 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-repeat: no-repeat;
}

.price {
  justify-content: space-between;
  width: 166px;
  margin: 24px 0 16px;
  line-height: 18px;
  display: flex;
}

.price__title {
  justify-content: space-between;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__number {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.footer {
  background: var(--blue);
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
}

.footer__button {
  color: var(--white);
  width: 27px;
  margin: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: inline-block;
}

.footer:hover {
  background: var(--white);
  border: 1px solid var(--blue);
  border-radius: 5px;
}

.footer__button:hover {
  color: var(--blue);
  text-decoration: none;
}
